" spin />\r\n                </div>\r\n\r\n                <div\r\n                    v-if=\"validationSuccess\"\r\n                    class=\"validation-success\"\r\n                >\r\n                    <font-awesome-icon icon=\"check\" />\r\n                </div>\r\n\r\n                <div\r\n                    v-if=\"validationFailure\"\r\n                    class=\"validation-failure\"\r\n                >\r\n                    {{ validatorMessage }}\r\n                </div>\r\n\r\n            </qrcode-stream>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport { BButton } from 'bootstrap-vue'\r\nimport { QrcodeStream } from 'vue-qrcode-reader'\r\nexport default {\r\n    components: {\r\n        QrcodeStream,\r\n        BButton\r\n    },\r\n    props: {\r\n        busy: Boolean,\r\n        enabled: Boolean,\r\n        validator: {\r\n            type: Function,\r\n            required: false,\r\n        },\r\n        validatorMessage: {\r\n            type: String,\r\n            required: false,\r\n            default: 'Invalid input.'\r\n        }\r\n    },\r\n    data() {\r\n        return {\r\n            errorMessage: undefined,\r\n            loading: false,\r\n            isEnabled: this.enabled,\r\n            camera: 'auto',\r\n            foo: null,\r\n            isValid: undefined\r\n        }\r\n    },\r\n    computed: {\r\n        buttonVariant() {\r\n            return this.isEnabled ? 'warning' : 'primary'\r\n        },\r\n        buttonIcon() {\r\n            return this.isEnabled ? 'times' : 'qrcode'\r\n        },\r\n        buttonLabel() {\r\n            return this.isEnabled ? 'Stop scanner' : 'Enable scanner'\r\n        },\r\n        validationSuccess() {\r\n            return this.isValid === true\r\n        },\r\n        validationFailure() {\r\n            return this.isValid === false\r\n        },\r\n\r\n    },\r\n    watch: {\r\n        isEnabled(val, oldVal) {\r\n            if (val && !oldVal) {\r\n                this.$emit('enable')\r\n            }\r\n            if (!val && oldVal) {\r\n                this.$emit('disable')\r\n            }\r\n        }\r\n    },\r\n    methods: {\r\n        onInit(promise) {\r\n            this.loading = true\r\n            this.errorMessage = undefined\r\n            this.resetValidationState()\r\n            promise\r\n                .catch(error => this.errorMessage = error.message)\r\n                .then(() => this.loading = false)\r\n        },\r\n        async onDecode(value) {\r\n            this.turnCameraOff()\r\n            await this.timeout(500)\r\n            this.isValid = !this.validator || this.validator(value)\r\n            if (this.isValid) {\r\n                this.$emit('decode', value)\r\n            }\r\n            await this.timeout(this.isValid ? 1000 : 2500)\r\n            this.turnCameraOn()\r\n        },\r\n        resetValidationState () {\r\n            this.isValid = undefined\r\n        },\r\n        turnCameraOn() {\r\n            this.camera = 'auto'\r\n        },\r\n        turnCameraOff() {\r\n            this.camera = 'off'\r\n        },\r\n        timeout(ms) {\r\n            return new Promise(resolve => window.setTimeout(resolve, ms))\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.error-message {\r\n    color: red;\r\n}\r\n.loading-indicator {\r\n    color: grey;\r\n}\r\n.loading-indicator,\r\n.validation-success,\r\n.validation-failure,\r\n.error-message {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n\r\n    background-color: rgba(255, 255, 255, .8);\r\n    text-align: center;\r\n    font-weight: bold;\r\n    font-size: 1.4rem;\r\n    padding: 10px;\r\n\r\n    display: flex;\r\n    flex-flow: column nowrap;\r\n    justify-content: center;\r\n}\r\n.validation-success {\r\n    color: green;\r\n}\r\n.validation-failure {\r\n    color: red;\r\n}\r\n</style>"],"sourceRoot":""}]);
